var jsonp = require('jsonp')

JSONDATA = 'https://yubinbango.github.io/yubinbango-data/data';
CACHE = [];


PREFMAP = [
    null,       '',   '',   '',   '',
    '',   '',   '',   '',   '',
    '',   '',   '',   '',   '',
    '',   '',   '',   '',   '',
    '',   '',   '',   '',   '',
    '',   '',   '',   '',   '',
    '', '',   '',   '',   '',
    '',   '',   '',   '',   '',
    '',   '',   '',   '',   '',
    '',   '', ''
];


exports.get = function (zip_code, callback) {
    // 7
    //    var zipoptimize = function(AjaxZip3.fzip1, AjaxZip3.fzip2){
  var vzip = zip_code;
  if (!vzip) return;

  // extract number only
  var nzip = '';
  for( var i=0;  i< vzip.length; i++ ) {
    var chr = vzip.charCodeAt(i);
    if ( chr < 48 ) continue;
    if ( chr > 57 ) continue;
    nzip += vzip.charAt(i);
  }
  if (nzip.length < 7 ) return;

  // fetch from cache data using upper 3 digit
  var zip3 = nzip.substr(0,3);
  var data = CACHE[zip3];
  if (data) return parse(nzip, data, callback);

  // fetch by jsonp
  fetchRemote(nzip, callback);
};

var parse = function(nzip, data, callback){
  var array = data[nzip];
  // Opera 0x00800000  +0xff000000 
  var opera = (nzip-0+0xff000000)+"";
  if ( ! array && data[opera] ) array = data[opera];
  if ( ! array ) return;
  var pref_id = array[0];                 // ID
  if ( ! pref_id ) return;
  var jpref = PREFMAP[pref_id];  // 
  if ( ! jpref ) return;
  var jcity = array[1];
  if ( ! jcity ) jcity = '';              // 
  var jarea = array[2];
  if ( ! jarea ) jarea = '';              // 
  var jstrt = array[3];
  if ( ! jstrt ) jstrt = '';              // 
  callback({
    'prefecture': jpref,
    'city': jcity,
    'area': jarea,
    'street': jstrt
  });
};

fetchRemote = function (nzip, callback) {
  var zip3 = nzip.substr(0,3);
  var url = JSONDATA+'/'+zip3+'.js';
  jsonp(url, { name: '$yubin'}, function(error, data) {
    if (!error) {
      CACHE[zip3] = data;
      parse(nzip, data, callback);
    }
  });
};
